:root {
  --background: #1d1429;
  --background-hover: #a57ed4;
  --color: #bb9ae6;

  --button: #ad96ca;
  --base-font-size: 16px;
  --output-color: #2d1649;
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
}

.app {
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 1fr 1fr;
  background: var(--background);
  height: 100vh;
  grid-template-rows: 80px auto;
  color: var(--color);
}

header {
  color: var(--color);

  grid-column: 1 / 3;

  border-bottom: solid salmon 2px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}

button {
  padding: 0.4rem;
  margin-right: 0.4rem;
  background: var(--button);
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background ease-in-out 0.3s;
}
button:hover {
  background: var(--background-hover);
}

.editor {
  box-sizing: border-box;
  background: var(--background);
  color: var(--color);
  border: none;
  font-size: var(--base-font-size);
  height: 100%;
  border-right: solid salmon 2px;
  position: relative;
}
.editor textarea {
  box-sizing: border-box;
  background: var(--background);
  color: var(--color);
  border: none;
  scrollbar-color: var(--background);
  width: 100%;
  height: 100%;
  padding: 1rem;
}

.tab {
  position: absolute;
  top: -40px;
  right: -2px;
  border: solid salmon 2px;
  border-bottom: none;
  padding: 5px;
  margin: 0;
  height: 26px;
}

.tab > p {
  margin: 0;
}
.output {
  padding: 1rem;
  border: solid salmon 2px;
  background: rgb(252, 237, 255);
}
.output h1 {
  color: var(--background);
}
img {
  width: 100%;
}
.output p {
  color: var(--output-color);
}
